import * as Notifications from 'expo-notifications'
import { NotificationTriggerInput } from 'expo-notifications'

interface NotificationConfig {
  title: string
  body: string
  data?: Record<string, unknown>
  sound?: boolean
  badge?: number
}

// ÈÄöÁü•„ÅÆÊ®©ÈôêÂèñÂæó
export const requestNotificationPermissions = async (): Promise<boolean> => {
  const { status } = await Notifications.requestPermissionsAsync()
  type PermissionStatus = 'granted' | 'denied' | 'undetermined'
  return (status as PermissionStatus) === 'granted'
}

// ÈÄöÁü•„ÅÆ„Çπ„Ç±„Ç∏„É•„Éº„É™„É≥„Ç∞
export const scheduleNotification = async (
  config: NotificationConfig,
  trigger: NotificationTriggerInput
): Promise<string> => {
  return await Notifications.scheduleNotificationAsync({
    content: {
      title: config.title,
      body: config.body,
      data: config.data,
      sound: config.sound,
      badge: config.badge,
    },
    trigger,
  })
}

// ÈÄöÁü•„ÅÆ„Ç≠„É£„É≥„Çª„É´
export const cancelNotification = async (identifier: string): Promise<void> => {
  await Notifications.cancelScheduledNotificationAsync(identifier)
}

// ÈÄöÁü•Ë®≠ÂÆö„ÅÆÂèñÂæó
export const getNotificationSettings =
  async (): Promise<Notifications.NotificationPermissionsStatus> => {
    return await Notifications.getPermissionsAsync()
  }

// ÈÄöÁü•„Éè„É≥„Éâ„É©„Éº„ÅÆË®≠ÂÆö
export const setNotificationHandler = (
  handler: (response: Notifications.NotificationResponse) => void
): void => {
  void Notifications.addNotificationResponseReceivedListener(handler)
}

// „Éê„ÉÉ„Ç∏Êï∞„ÅÆÊõ¥Êñ∞
export const setBadgeCount = async (count: number): Promise<void> => {
  await Notifications.setBadgeCountAsync(count)
}

// ÈÄöÁü•„ÅÆÂàùÊúüË®≠ÂÆö
export const initializeNotifications = (): void => {
  Notifications.setNotificationHandler({
    handleNotification: async () => {
      await Promise.resolve() // dummy await to satisfy the linter
      return {
        shouldShowAlert: true,
        shouldPlaySound: true,
        shouldSetBadge: true,
      }
    },
  })
}
