import { useState, useEffect, useCallback } from 'react'
import AsyncStorage from '@react-native-async-storage/async-storage'
import { KebabRecord, KebabRecordInput } from '../types/record'
import { useNotifications } from './useNotifications'
import { kebabRecordSchema } from '../schemas/record'

const STORAGE_KEY = '@kebab_records'

export interface KebabStats {
  consecutiveDays: number
  totalCount: number
}

type OperationResult<T = undefined> =
  | { success: true; data?: T }
  | { success: false; error: string }

type StorageData = {
  records: KebabRecord[]
  version: number
}

export const useKebabRecords = () => {
  const { addNotification } = useNotifications()
  const [records, setRecords] = useState<KebabRecord[]>([])
  const [stats, setStats] = useState<KebabStats>({
    consecutiveDays: 0,
    totalCount: 0,
  })

  const calculateStats = useCallback((records: KebabRecord[]) => {
    // ÂêàË®àÊï∞„ÅÆË®àÁÆó
    const totalCount = records.length

    // ÈÄ£Á∂öÊó•Êï∞„ÅÆË®àÁÆó
    const sortedDates = [...new Set(records.map((r) => r.createdAt.split('T')[0]))].sort(
      (a, b) => new Date(b).getTime() - new Date(a).getTime()
    )

    let consecutiveDays = 0
    const today = new Date().toISOString().split('T')[0]
    let currentDate = new Date(today)

    for (const date of sortedDates) {
      const recordDate = new Date(date)
      const diffDays = Math.floor(
        (currentDate.getTime() - recordDate.getTime()) / (1000 * 60 * 60 * 24)
      )

      if (diffDays <= 1) {
        consecutiveDays++
        currentDate = recordDate
      } else {
        break
      }
    }

    setStats({
      consecutiveDays,
      totalCount,
    })
  }, [])

  const loadRecords = useCallback(async (): Promise<OperationResult> => {
    try {
      const jsonValue = await AsyncStorage.getItem(STORAGE_KEY)
      if (jsonValue != null) {
        const data = JSON.parse(jsonValue) as StorageData | KebabRecord[]
        // Âè§„ÅÑÂΩ¢Âºè„ÅÆ„Éá„Éº„Çø‰∫íÊèõÊÄßÂØæÂøú
        const loadedRecords = Array.isArray(data) ? data : data.records
        setRecords(loadedRecords)
        calculateStats(loadedRecords)
        return { success: true }
      }
      return { success: true }
    } catch (e) {
      console.error('Error loading records:', e)
      return {
        success: false,
        error: e instanceof Error ? e.message : 'Ë®òÈå≤„ÅÆË™≠„ÅøËæº„Åø‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
      }
    }
  }, [calculateStats])

  const addRecord = useCallback(
    async (input: KebabRecordInput): Promise<OperationResult<KebabRecord>> => {
      try {
        // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥
        kebabRecordSchema.parse(input)

        const newRecord: KebabRecord = {
          id: Date.now().toString(),
          ...input,
          createdAt: new Date().toISOString(),
        }

        const updatedRecords = [...records, newRecord]
        await AsyncStorage.setItem(STORAGE_KEY, JSON.stringify(updatedRecords))
        setRecords(updatedRecords)
        calculateStats(updatedRecords)

        // ÈÄöÁü•„Çí‰ΩúÊàê
        try {
          await addNotification({
            type: 'record',
            title: 'Êñ∞Ë¶èË®òÈå≤',
            message: `${input.kebabType === 'kebab' ? '„Ç±„Éê„Éñ' : '„Ç±„Éê„Éñ‰∏º'}„ÇíË®òÈå≤„Åó„Åæ„Åó„ÅüÔºÅ`,
          })
        } catch (error) {
          console.error('Failed to add notification:', error)
        }

        return { success: true, data: newRecord }
      } catch (error) {
        console.error('Error adding record:', error)
        return {
          success: false as const,
          error: error instanceof Error ? error.message : '‰∏çÊòé„Å™„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [records, calculateStats, addNotification]
  )

  const updateRecord = useCallback(
    async (id: string, input: KebabRecordInput): Promise<OperationResult<KebabRecord>> => {
      try {
        // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥
        kebabRecordSchema.parse(input)

        const recordIndex = records.findIndex((r) => r.id === id)
        if (recordIndex === -1) {
          return {
            success: false as const,
            error: 'ÊåáÂÆö„Åï„Çå„Åü„É¨„Ç≥„Éº„Éâ„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„Åß„Åó„Åü',
          }
        }

        const updatedRecord: KebabRecord = {
          ...records[recordIndex],
          ...input,
        }

        const updatedRecords = [...records]
        updatedRecords[recordIndex] = updatedRecord
        await AsyncStorage.setItem(STORAGE_KEY, JSON.stringify(updatedRecords))
        setRecords(updatedRecords)
        calculateStats(updatedRecords)

        // ÈÄöÁü•„Çí‰ΩúÊàê
        try {
          await addNotification({
            type: 'record',
            title: 'Ë®òÈå≤„ÇíÊõ¥Êñ∞',
            message: `${input.kebabType === 'kebab' ? '„Ç±„Éê„Éñ' : '„Ç±„Éê„Éñ‰∏º'}„ÅÆË®òÈå≤„ÇíÊõ¥Êñ∞„Åó„Åæ„Åó„ÅüÔºÅ`,
          })
        } catch (error) {
          console.error('Failed to add notification:', error)
        }

        return { success: true, data: updatedRecord }
      } catch (error) {
        console.error('Error updating record:', error)
        return {
          success: false as const,
          error: error instanceof Error ? error.message : '‰∏çÊòé„Å™„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [records, calculateStats, addNotification]
  )

  const clearRecords = useCallback(async (): Promise<OperationResult> => {
    try {
      await AsyncStorage.removeItem(STORAGE_KEY)
      setRecords([])
      setStats({
        consecutiveDays: 0,
        totalCount: 0,
      })
      return { success: true }
    } catch (e) {
      console.error('Error clearing records:', e)
      return {
        success: false,
        error: e instanceof Error ? e.message : 'Ë®òÈå≤„ÅÆ„ÇØ„É™„Ç¢‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
      }
    }
  }, [])

  useEffect(() => {
    void loadRecords()
  }, [loadRecords])

  return {
    records,
    stats,
    addRecord,
    clearRecords,
    loadRecords,
    updateRecord,
  }
}
