import { Alert } from 'react-native'
import { useState, useEffect, useCallback } from 'react'
import AsyncStorage from '@react-native-async-storage/async-storage'
import { Notification, NotificationState, DEFAULT_REMINDER_SETTINGS } from '../types/notification'

type OperationResult<T = undefined> =
  | { success: true; data?: T }
  | { success: false; error: string }

type ReminderNotificationConfig = {
  title: string
  body: string
  sound: boolean
  badge: number
  data: {
    type: string
    action: string
  }
}

import type { CalendarTriggerInput } from 'expo-notifications'

type ReminderScheduleConfig = CalendarTriggerInput & {
  repeats: boolean
}
import {
  scheduleNotification,
  cancelNotification,
  requestNotificationPermissions,
} from '../services/notification'

const STORAGE_KEY = '@notifications'
const SETTINGS_KEY = '@notification_settings'
const REMINDER_SETTINGS_KEY = '@reminder_settings'

const DEFAULT_STATE: NotificationState = {
  notifications: [],
  enabled: false,
  reminder: DEFAULT_REMINDER_SETTINGS,
}

export const useNotifications = () => {
  const [state, setState] = useState<NotificationState>(DEFAULT_STATE)

  const loadNotifications = useCallback(async (): Promise<OperationResult> => {
    try {
      const [notificationsJson, settingsJson, reminderJson] = await Promise.all([
        AsyncStorage.getItem(STORAGE_KEY),
        AsyncStorage.getItem(SETTINGS_KEY),
        AsyncStorage.getItem(REMINDER_SETTINGS_KEY),
      ])

      const notifications = notificationsJson
        ? (JSON.parse(notificationsJson) as Notification[])
        : []
      const settings = settingsJson
        ? (JSON.parse(settingsJson) as { enabled: boolean })
        : { enabled: false }
      const reminder = reminderJson
        ? (JSON.parse(reminderJson) as typeof DEFAULT_REMINDER_SETTINGS)
        : DEFAULT_REMINDER_SETTINGS

      setState((prev) => ({
        ...prev,
        notifications,
        enabled: settings.enabled,
        reminder,
      }))
      return { success: true }
    } catch (e) {
      console.error('Error loading notifications:', e)
      return {
        success: false,
        error: e instanceof Error ? e.message : 'ÈÄöÁü•„ÅÆË™≠„ÅøËæº„Åø‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
      }
    }
  }, [])

  const scheduleReminderNotification = useCallback(
    async (time: string): Promise<OperationResult<string>> => {
      try {
        // Êó¢Â≠ò„ÅÆ„É™„Éû„Ç§„É≥„ÉÄ„Éº„Çí„Ç≠„É£„É≥„Çª„É´
        if (state.reminder.notificationId) {
          await cancelNotification(state.reminder.notificationId)
        }

        const [hours, minutes] = time.split(':').map(Number)
        const config: ReminderNotificationConfig = {
          title: 'ü•ô „Ç±„Éê„Éñ„ÅÆË®òÈå≤„Çí„ÅäÂøò„Çå„Å™„Åè',
          body: '‰ªäÊó•„ÅÆ„Ç±„Éê„ÉñË®òÈå≤„ÅØ„ÇÇ„ÅÜ„Å§„Åë„Åæ„Åó„Åü„ÅãÔºü',
          sound: true,
          badge: 1,
          data: {
            type: 'reminder',
            action: 'record',
          },
        }

        const schedule: ReminderScheduleConfig = {
          hour: hours,
          minute: minutes,
          repeats: true,
        }

        const notificationId = await scheduleNotification(config, schedule)

        const updatedReminder = {
          ...state.reminder,
          notificationId,
          time,
        }

        await AsyncStorage.setItem(REMINDER_SETTINGS_KEY, JSON.stringify(updatedReminder))
        setState((prev) => ({
          ...prev,
          reminder: updatedReminder,
        }))
        return { success: true, data: notificationId }
      } catch (e) {
        console.error('Error scheduling reminder:', e)
        return {
          success: false,
          error:
            e instanceof Error
              ? e.message
              : '„É™„Éû„Ç§„É≥„ÉÄ„Éº„ÅÆ„Çπ„Ç±„Ç∏„É•„Éº„É´Ë®≠ÂÆö‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [state.reminder]
  )

  const addNotification = useCallback(
    async (
      notification: Omit<Notification, 'id' | 'createdAt' | 'read'>
    ): Promise<OperationResult<Notification>> => {
      try {
        const newNotification: Notification = {
          id: Date.now().toString(),
          ...notification,
          createdAt: new Date().toISOString(),
          read: false,
        }

        const updatedNotifications = [newNotification, ...state.notifications]
        await AsyncStorage.setItem(STORAGE_KEY, JSON.stringify(updatedNotifications))
        setState((prev) => ({
          ...prev,
          notifications: updatedNotifications,
        }))
        return { success: true, data: newNotification }
      } catch (e) {
        console.error('Error adding notification:', e)
        return {
          success: false,
          error: e instanceof Error ? e.message : 'ÈÄöÁü•„ÅÆËøΩÂä†‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [state.notifications]
  )

  const markAsRead = useCallback(
    async (id: string): Promise<OperationResult> => {
      try {
        const updatedNotifications = state.notifications.map((notification: Notification) =>
          notification.id === id ? { ...notification, read: true } : notification
        )
        await AsyncStorage.setItem(STORAGE_KEY, JSON.stringify(updatedNotifications))
        setState((prev) => ({
          ...prev,
          notifications: updatedNotifications,
        }))
        return { success: true }
      } catch (e) {
        console.error('Error marking notification as read:', e)
        return {
          success: false,
          error: e instanceof Error ? e.message : 'ÈÄöÁü•„ÅÆÊó¢Ë™≠Ë®≠ÂÆö‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [state.notifications]
  )

  const clearNotifications = useCallback(async (): Promise<OperationResult> => {
    try {
      await AsyncStorage.setItem(STORAGE_KEY, JSON.stringify([]))
      setState((prev) => ({
        ...prev,
        notifications: [],
      }))
      return { success: true }
    } catch (e) {
      console.error('Error clearing notifications:', e)
      return {
        success: false,
        error: e instanceof Error ? e.message : 'ÈÄöÁü•„ÅÆ„ÇØ„É™„Ç¢‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
      }
    }
  }, [])

  const toggleNotifications = useCallback(
    async (enabled: boolean): Promise<OperationResult> => {
      try {
        if (enabled) {
          const permissionGranted = await requestNotificationPermissions()
          if (!permissionGranted) {
            return {
              success: false,
              error: 'ÈÄöÁü•„ÅÆÊ®©Èôê„Åå‰ªò‰∏é„Åï„Çå„Åæ„Åõ„Çì„Åß„Åó„Åü',
            }
          }
        }

        const currentReminder = state.reminder
        const updatedReminder = {
          ...currentReminder,
          enabled: enabled ? currentReminder.enabled : false,
        }

        // ÈÄöÁü•„ÅÆ„Çπ„Ç±„Ç∏„É•„Éº„É´Êõ¥Êñ∞
        if (enabled && currentReminder.enabled) {
          await scheduleReminderNotification(currentReminder.time)
        } else if (!enabled && currentReminder.notificationId) {
          await cancelNotification(currentReminder.notificationId)
        }

        // ÈÄöÁü•Ë®≠ÂÆö„Çí‰øùÂ≠ò
        await AsyncStorage.setItem(SETTINGS_KEY, JSON.stringify({ enabled }))
        await AsyncStorage.setItem(REMINDER_SETTINGS_KEY, JSON.stringify(updatedReminder))

        // Áä∂ÊÖã„ÇíÊõ¥Êñ∞
        setState((prev) => ({
          ...prev,
          enabled,
          reminder: updatedReminder,
        }))
        return { success: true }
      } catch (e) {
        console.error('Error toggling notifications:', e)
        return {
          success: false,
          error: e instanceof Error ? e.message : 'ÈÄöÁü•Ë®≠ÂÆö„ÅÆÊõ¥Êñ∞‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [state.reminder, scheduleReminderNotification]
  )

  const toggleReminder = useCallback(
    async (enabled: boolean): Promise<OperationResult> => {
      try {
        // ÈÄöÁü•„ÅåÁÑ°Âäπ„ÅÆÂ†¥Âêà„ÅØ„ÄÅ„É™„Éû„Ç§„É≥„ÉÄ„Éº„ÇíÊúâÂäπ„Å´„Åß„Åç„Å™„ÅÑ
        if (!state.enabled) {
          Alert.alert('„Ç®„É©„Éº', 'ÈÄöÁü•„ÇíÊúâÂäπ„Å´„Åó„Å¶„Åè„Å†„Åï„ÅÑ')
          return {
            success: false,
            error: 'ÈÄöÁü•„ÅåÁÑ°Âäπ„ÅÆ„Åü„ÇÅ„ÄÅ„É™„Éû„Ç§„É≥„ÉÄ„Éº„ÇíË®≠ÂÆö„Åß„Åç„Åæ„Åõ„Çì',
          }
        }

        const updatedReminder = {
          ...state.reminder,
          enabled,
        }

        // ÈÄöÁü•„ÅÆ„Çπ„Ç±„Ç∏„É•„Éº„É´Êõ¥Êñ∞
        if (enabled) {
          await scheduleReminderNotification(updatedReminder.time)
        } else if (state.reminder.notificationId) {
          await cancelNotification(state.reminder.notificationId)
        }

        // „É™„Éû„Ç§„É≥„ÉÄ„ÉºË®≠ÂÆö„Çí‰øùÂ≠ò
        await AsyncStorage.setItem(REMINDER_SETTINGS_KEY, JSON.stringify(updatedReminder))

        // Áä∂ÊÖã„ÇíÊõ¥Êñ∞
        setState((prev) => ({
          ...prev,
          reminder: updatedReminder,
        }))
        return { success: true }
      } catch (e) {
        console.error('Error toggling reminder:', e)
        Alert.alert('„Ç®„É©„Éº', '„É™„Éû„Ç§„É≥„ÉÄ„Éº„ÅÆË®≠ÂÆö„Å´Â§±Êïó„Åó„Åæ„Åó„Åü')
        return {
          success: false,
          error: e instanceof Error ? e.message : '„É™„Éû„Ç§„É≥„ÉÄ„ÉºË®≠ÂÆö„ÅÆÊõ¥Êñ∞‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [state.reminder, state.enabled, scheduleReminderNotification]
  )

  const updateReminderTime = useCallback(
    async (time: string): Promise<OperationResult> => {
      try {
        const updatedReminder = {
          ...state.reminder,
          time,
        }

        // ÈÄöÁü•„ÅåÊúâÂäπ„Å™Â†¥Âêà„ÅØÂÜç„Çπ„Ç±„Ç∏„É•„Éº„É´
        if (state.enabled && updatedReminder.enabled) {
          await scheduleReminderNotification(time)
        }

        // „É™„Éû„Ç§„É≥„ÉÄ„ÉºË®≠ÂÆö„Çí‰øùÂ≠ò
        await AsyncStorage.setItem(REMINDER_SETTINGS_KEY, JSON.stringify(updatedReminder))

        // Áä∂ÊÖã„ÇíÊõ¥Êñ∞
        setState((prev) => ({
          ...prev,
          reminder: updatedReminder,
        }))
        return { success: true }
      } catch (e) {
        console.error('Error updating reminder time:', e)
        return {
          success: false,
          error: e instanceof Error ? e.message : '„É™„Éû„Ç§„É≥„ÉÄ„ÉºÊôÇÂàª„ÅÆÊõ¥Êñ∞‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü',
        }
      }
    },
    [state.reminder, state.enabled, scheduleReminderNotification]
  )

  useEffect(() => {
    void loadNotifications()
  }, [loadNotifications])

  return {
    notifications: state.notifications,
    enabled: state.enabled,
    reminder: state.reminder,
    addNotification,
    markAsRead,
    clearNotifications,
    toggleNotifications,
    toggleReminder,
    updateReminderTime,
  }
}
